import $ from "jquery";

$(function() {
    //перенос контента в бургер
    function burgerContent() {
        if(window.innerWidth <= 1024) {
            $('.header__contacts').appendTo('.header__nav');
            $('.header__socials').appendTo('.header__nav');
        }
        else if (window.innerWidth > 1024) {
            $('.header__contacts').insertAfter('.header__search');
            $('.header__socials').insertAfter('.header__contacts');
        }
    }
    //активация бургера
    function burgerToggle() {
        $('body').on('click', '.js-burger', function () {
            if($(this).hasClass('_active')) {
                $(this).removeClass('_active');
                bodyScrollLock.clearAllBodyScrollLocks();
            }
            else {
                $(this).addClass('_active');
                bodyScrollLock.disableBodyScroll($(this));
            }
        });

        $(document).mouseup(function (e){
            var burgerMenu = $(".header__nav");
            if (!burgerMenu.is(e.target)
                && burgerMenu.has(e.target).length === 0
                && !$('.js-burger').is(e.target)) {
                $('.js-burger').removeClass('_active');
                bodyScrollLock.clearAllBodyScrollLocks();
            }
        });
    }
    burgerContent();
    burgerToggle();

    $(window).on('resize', function () {
        burgerContent();
        burgerToggle();
    })
});